import React, { Component } from 'react'

export default class resume extends Component{
    render(){
        return(
            <section id="resume" className="s-resume target-section">
                <div className="row s-resume__section">
                    <div className="column large-3 tab-12">
                    <h3 className="section-header-allcaps">Experiencia</h3>
                    </div>
                    <div className="column large-9 tab-12">
                    <div className="resume-block">
                        <div className="resume-block__header">
                        <h4 className="h3">PwC</h4>
                        <p className="resume-block__header-meta">
                            <span>Asistente de desarrollo</span> 
                            <span className="resume-block__header-date">
                            Julio 2020 - Nov 2020
                            </span>
                        </p>
                        </div>
                        <p>
                            Durante mi estadía en PwC realicé diversos proyectos de automatización para auditoría,
                            los cuales requerían distintas tecnologías debido a su dificultad.
                        </p>
                        <p>
                            Algunas de estas fueron:
                        </p>
                        <ul>
                            <li type="square">Selenium C#</li>
                            <li type="square">Macros para Excel (Usando VBA)</li>
                            <li type="square">ETL con Alteryx y Knime</li>
                        </ul>
                    </div> {/* end resume-block */}
                </div>
                </div> {/* s-resume__section */}
                <div className="row s-resume__section">
                    <div className="column large-3 tab-12">
                    <h3 className="section-header-allcaps">Educación</h3>
                    </div>
                    <div className="column large-9 tab-12">
                    <div className="resume-block">
                        <div className="resume-block__header">
                        <h4 className="h3">DuocUC</h4>
                        <p className="resume-block__header-meta">
                            <span>Analista programador computacional</span> 
                            <span className="resume-block__header-date">
                            Noviembre 2020
                            </span>
                        </p>
                        </div>
                        <p>
                        Estudié Analista Programador Computacional en DuocUC entre 2018 y 2020,
                        allí aprendí muchas herramientas y perfeccioné mis habilidades en gran medida
                        para varios lenguajes.
                        </p>
                        <p>
                        Destaco de estos aprendizajes lo siguiente:
                        </p>
                        <ul>
                            <li type="square">Java, C# y Python</li>
                            <li type="square">SQL y modelamiento de bases de datos</li>
                            <li type="square">Ingeniería de software</li>
                        </ul>
                    </div> {/* end resume-block */}
                    <div className="resume-block">
                        <div className="resume-block__header">
                        <h4 className="h3">Colegio San Luis Beltrán</h4>
                        <p className="resume-block__header-meta">
                            <span>Técnico nivel medio en Telecomunicaciones</span> 
                            <span className="resume-block__header-date">
                            Diciembre 2016
                            </span>
                        </p>
                        </div>
                        <p>
                        Estudié la rama técnica en mi colegio, aquí aprendí fundamentos de networking,
                        routing con Cisco, cableado estructurado, protocolos de comunicación, entre otros.
                        </p>
                    </div> {/* end resume-block */}
                    </div>
                </div> {/* s-resume__section */}
                <div className="row s-resume__section">
                    <div className="column large-3 tab-12">
                    <h3 className="section-header-allcaps">Habilidades técnicas</h3>
                    </div>
                    <div className="column large-9 tab-12">
                    <div className="resume-block">
                        <p>
                        Mis principales skills son:
                        </p>
                        <ul className="skill-bars-fat">
                        <li>
                            <div className="progress percent85" />
                            <strong>C#</strong>
                        </li>
                        <li>
                            <div className="progress percent60" />
                            <strong>Javascript</strong>
                        </li>
                        <li>
                            <div className="progress percent40" />
                            <strong>Python</strong>
                        </li>
                        <li>
                            <div className="progress percent55" />
                            <strong>HTML</strong>
                        </li>
                        <li>
                            <div className="progress percent30" />
                            <strong>CSS</strong>
                        </li>
                        <li>
                            <div className="progress percent35" />
                            <strong>React</strong>
                        </li>
                        <li>
                            <div className="progress percent50" />
                            <strong>NodeJS</strong>
                        </li>
                        <li>
                            <div className="progress percent50" />
                            <strong>Express.js</strong>
                        </li>
                        </ul>
                    </div> {/* end resume-block */}
                    </div>
                </div> {/* s-resume__section */}
                </section>

        )
    }
}